import { v, w } from '@dojo/framework/widget-core/d';
import WidgetBase from '@dojo/framework/widget-core/WidgetBase';
import esriLoader from 'esri-loader';

class App extends WidgetBase {

	private _map: any;

	protected async onAttach() {
		console.log('onAttach', esriLoader);
		await esriLoader.loadCss('https://js.arcgis.com/3.26/esri/css/main.css');
		const [Map] = await esriLoader.loadModules(['esri/map'], {
			url: 'https://js.arcgis.com/3.26/'
		});
		this._map = new Map('mapNode', {
			center: [-118, 34.5],
			zoom: 8,
			basemap: 'dark-gray'
		});
		console.log(this._map);
	}

	protected render() {
		return v('div', { key: 'root' }, [
			v('h1', ['Hello World']),
			v('div', { id: 'mapNode'})
		]);
	}
}

export default App;



